import { createStore } from "vuex";

export default createStore({
  state: {
    count: 0,
  },
  mutations: {
    devChangeData(state, value) {
      state.count = value;
    },
  },
  getters:{
    getCount(state){
      return state.count
    }
  },
  actions: {
    increaseCount(context) {
      context.commit("devChangeData", this.getters.getCount + 1);
    },
    decreaseCount(context) {
      context.commit("devChangeData", this.getters.getCount - 1);
    },
  },
  modules: {},
});
